@import "./variables.scss";

* {
  padding: 0;
  margin: 0;
  list-style: none;
  outline: none;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  position: relative;
  font-family: "Nunito Sans", sans-serif;
  background-color: $salomie-color;

  .app {
    padding-top: 20px;
    margin: 0 auto;

    .wrapper {
      width: calc(100vw - 100px);
      height: 100%;
      background-color: $white-color;
      margin: 50px auto;
      border-radius: 10px;
      max-width: 1400px;
    }
  }
}
